import React from 'react';
import PropTypes from 'prop-types';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import SimpleCell from '@vkontakte/vkui/dist/components/SimpleCell/SimpleCell';
import ContentCard from '@vkontakte/vkui/dist/components/ContentCard/ContentCard';
import CardGrid from '@vkontakte/vkui/dist/components/CardGrid/CardGrid';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';

const Home = ({ id, go, list }) => (
	<Panel id={id}>
		<PanelHeader>Clientlist</PanelHeader>
		<Group>
	    {list && list.map(item => {
			return (
				<Link href={"https://clientlist.ru/to/" + item.client._id}>
					<SimpleCell
						description={item.text}
			          	before={<Avatar src={item.client.avatar}/>}
			        >{item.client.name}
			        </SimpleCell>
		        </Link>
			);
	    })}
	    </Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	list: PropTypes.array
};

export default Home;
